* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* ---- MOBILE INTERGRATION ---- */
body {
  height: 100vh;
  width: 375px;
}

header {
  height: 75px;
  width: 375px;

  display: flex;
  align-items: center;
  justify-content: space-around;
}
header div {
  display: flex;
  justify-content: space-between;
}

.headerIcon {
  margin-right: 18px;

  transform: translateY(4px);
}

#imgAvatar {
  height: 24px;
  width: 24px;
}

.company {
  color: hsl(26, 100%, 55%);
}

article {
  margin: 0% 5% 0% 5%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

footer div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

article section footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

figure {
  background-image: url("../images/image-product-1.jpg");
  background-size: cover;
  width: 100%;
  height: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
